.popup {
	display: flex;
    background-color: rgba(0,0,0,.5);
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	justify-content: center;
	align-items: center;
	opacity: 0;
	visibility: hidden;
    transition: visibility 0.5s, opacity 0.3s linear;
}

       
    
    
    